"use client";

import * as React from "react";
import { format } from "date-fns";
import Link from "next/link";
import { AdminPageShell } from "@prosfin/ui";
import { AppDataTable } from "@/components/admin/data-table/app-data-table";
import { Badge } from "@prosfin/ui";
import { listClients } from "@/lib/data/clients";
import type { Client } from "@/types/admin";
import type { DataTableColumn } from "@/components/admin/data-table/types";

const statusColors: Record<Client["status"], string> = {
  active: "bg-green-100 text-green-800",
  inactive: "bg-gray-100 text-gray-800",
  onboarding: "bg-yellow-100 text-yellow-800",
};

export default function ClientsPage() {
  const [clients, setClients] = React.useState<Client[]>([]);
  const [isLoading, setIsLoading] = React.useState(true);

  React.useEffect(() => {
    listClients().then((data) => {
      setClients(data);
      setIsLoading(false);
    });
  }, []);

  const columns: DataTableColumn<Client>[] = [
    {
      id: "companyName",
      header: "Company",
      accessorKey: "companyName",
      enableSorting: true,
      cell: ({ row }) => (
        <Link
          href={`/clients/${row.original.id}`}
          className="font-medium hover:underline"
        >
          {row.original.companyName}
        </Link>
      ),
    },
    {
      id: "contactName",
      header: "Contact",
      accessorKey: "contactName",
    },
    {
      id: "email",
      header: "Email",
      accessorKey: "email",
    },
    {
      id: "phone",
      header: "Phone",
      accessorKey: "phone",
    },
    {
      id: "status",
      header: "Status",
      accessorKey: "status",
      cell: ({ row }) => (
        <Badge className={statusColors[row.original.status]}>
          {row.original.status}
        </Badge>
      ),
    },
    {
      id: "createdAt",
      header: "Created",
      accessorKey: "createdAt",
      cell: ({ row }) =>
        format(new Date(row.original.createdAt), "MMM dd, yyyy"),
    },
  ];

  return (
    <AdminPageShell
      title="Clients"
      description="Manage your clients"
    >
      <AppDataTable
        columns={columns}
        data={clients}
        isLoading={isLoading}
        searchPlaceholder="Search clients..."
        emptyMessage="No clients found"
      />
    </AdminPageShell>
  );
}
